import React, { useState } from "react";
import { motion } from "framer-motion";
import axios from "axios";

const Form = ({
  inputs,
  actionBtnText,
  selectedNavIndex,
  submitHandler,
  setResults,
  darkMode
}) => {
  const [isInitialLoad, setIsInitialLoad] = useState(true);
  const [formData, setFormData] = useState({});

  const [formMsg, setFormMsg] = useState(false);

  const handleAnimationComplete = () => {
    setIsInitialLoad(false);
  };

  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [id]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    console.log()
    if (isNaN(parseFloat(formData["input-0"])) || isNaN(parseFloat(formData["input-1"]))|| isNaN(parseFloat(formData["input-2"])) ||isNaN(parseFloat(formData["input-3"])) ||isNaN(parseFloat(formData["input-4"])) || isNaN(parseFloat(formData["input-5"]))|| isNaN(parseFloat(formData["input-6"]))) {
      setFormMsg(true)

      setTimeout(()=> {
        setFormMsg(false)
      },3000)

      
      
    } else {
      
      submitHandler();

      const payload = {
        design: parseFloat(formData["input-0"]) || 0,
        Input: parseFloat(formData["input-1"]) || 0,
        Isc: parseFloat(formData["input-2"]) || 0,
        Voc: parseFloat(formData["input-3"]) || 0,
        Im: parseFloat(formData["input-4"]) || 0,
        Vm: parseFloat(formData["input-5"]) || 0,
        Pm: parseFloat(formData["input-6"]) || 0,
        Variant: formData["input-7"] || "",
      };

      try {
        const response = await axios.post(
          "http://127.0.0.1:5000/predict",
          payload
        );
        console.log("Prediction result:", response.data);

        setResults([response.data.default_status, response.data.default_class]);
        // Handle the response here (e.g., update state, show result to user)
      } catch (error) {
        console.error("Error making prediction:", error);
        // Handle errors here (e.g., show error message to user)
      }
    }
  };

  return (
    <motion.div
      className="form-container"
      initial={{ opacity: 0 }}
      animate={selectedNavIndex === 2 ? { opacity: 1 } : { opacity: 0 }}
      transition={{ duration: 0.5 }}
      onAnimationComplete={handleAnimationComplete}
    >
      <form onSubmit={handleSubmit}>
        {inputs.map((input, index) => (
          <motion.div
            key={index}
            className="input-container"
            initial={{ opacity: 0 }}
            animate={selectedNavIndex === 2 ? { opacity: 1 } : { opacity: 0 }}
            transition={{ delay: (index + 2) * 0.1, duration: 0.5 }}
          >
            <label htmlFor={`input-${index}`}>{input.label}</label>
            <input
              id={`input-${index}`}
              className={ darkMode ? "grey" : "black"}
              type={input.type}
              placeholder={input.placeholder}
              onChange={handleInputChange}
              value={formData[`input-${index}`] || ""}
            />
          </motion.div>
        ))}

        <div className="submit-container my-20 ">
          <motion.p className="small-text" initial={{opacity:0}} animate={formMsg ? {opacity:1}:{opacity:0}}>Please Fill All The Fields</motion.p>
          <button type="submit">{actionBtnText}</button>
        </div>
      </form>
    </motion.div>
  );
};

export default Form;
