import "./App.css";
import Header from "./Componenets/Header";
import logoImg from "./assets/logo.png";
import { useState, useEffect, useRef } from "react";
import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";
import python from "./assets/python.png";
import pyplot from "./assets/pyplot.png";
import pycharm from "./assets/pycharm.png";
import sklearn from "./assets/sklearn.png";
import pc from "./assets/pc.png";
import Form from "./Componenets/Form";
import useDarkMode from "use-dark-mode";
import PageIndicator from "./Componenets/PgaeIndicator";
import OverlayContainer from "./Componenets/OverlayContainer";
import { animations } from "./animations/animations";
import loader from "./assets/loader.gif";
import phone from "./assets/phone.png";

function App() {
  const [isOverlayOpen, setIsOverlayOpen] = useState(false);
  const [scrollPosition, setScrollPosition] = useState(0);

  const [results, setResults] = useState([10, 0]);
  const darkMode = useDarkMode(false);

  const [currentStep, setCurrentStep] = useState(0);

  const navLinks = [
    "Home",
    "Model",
    "Form",
    "Image-Based Prediction",
    "Contact",
  ];
  const textColor = "black";
  const actionBtnBgColor = "#A1DD70";

  const toggleOverlay = () => {
    console.log("overlay toggled");
    setIsOverlayOpen(!isOverlayOpen);
  };

  const inputs = [
    { label: "Design", type: "text", placeholder: "Enter Your Ns Here" },
    { label: "Input", type: "text", placeholder: "Enter Your Isc Here" },
    { label: "Isc", type: "text", placeholder: "Enter Your Voc Here" },
    { label: "Voc", type: "text", placeholder: "Enter Your Im Here" },
    { label: "Im", type: "text", placeholder: "Enter Your Vm Here" },
    { label: "Vm", type: "text", placeholder: "Enter Your Pm Here" },
    { label: "Pm", type: "text", placeholder: "Enter Your Iph Here" },
    { label: "Variant", type: "text", placeholder: "Enter Your Rs Here" },
  ];

  const [selectedNavIndex, setSelectedNavIndex] = useState(0);

  const [realClass, setRealClass] = useState("-");
  const [showLoader, setShowLoader] = useState(false);
  const [showRealClass, setShowRealClass] = useState(false);

  const [predcitedClass, setPredcitedClass] = useState("-");

  const [imageName, setImageName] = useState("No image selected");
  const [imageSrc, setImageSrc] = useState(null);

  useEffect(() => {
    let resetTimer;

    if (showLoader) {
      setCurrentStep(1);

      const secondStepTimer = setTimeout(() => {
        setCurrentStep(2);
      }, 1500); // 1 second delay + 0.5 second fade-out for the first message

      const thirdStepTimer = setTimeout(() => {
        setCurrentStep(3);
      }, 3000); // 1 second delay + 0.5 second fade-out for the second message

      return () => {
        clearTimeout(secondStepTimer);
        clearTimeout(thirdStepTimer);
      };
    } else {
      resetTimer = setTimeout(() => {
        setCurrentStep(0);
      }, 2000); // Reset to initial state if showLoader is false
    }
  }, [showLoader]);
  useEffect(() => {
    if (imageSrc !== null) {
      // Ensure imageSrc is not null before proceeding
      setShowLoader(true);
      setShowRealClass(false);

      // Simulate fetching real class data after image is selected

      var timer = setTimeout(async () => {
        setShowLoader(false);
        setShowRealClass(true);

        var prediction = await getPredictionByImageName(imageName);

        if (prediction !== null && prediction !== undefined) {
          setRealClass(prediction.true_class);
        } else {
          setRealClass(null);
        }

        // Replace with actual class from your prediction data
      }, 2000); // 2 seconds delay

      timer = setTimeout(async () => {
        setShowLoader(false);
        setShowRealClass(true);

        var prediction = await getPredictionByImageName(imageName);

        if (prediction !== null && prediction !== undefined) {
          setPredcitedClass(prediction.predicted_class);

          console.log("set predicted" + prediction.predicted_class);
        } else {
          setPredcitedClass(null);
          console.log("null predicted");
        }

        // Replace with actual class from your prediction data
      }, 4000);

      return () => clearTimeout(timer);
    }
  }, [imageSrc]);

  const sectionRefs = [
    useRef(null),
    useRef(null),
    useRef(null),
    useRef(null),
    useRef(null),
  ];

  const controls = useAnimation();
  const [ref, inView] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });

  const handleNavLinkClick = (index) => {
    setSelectedNavIndex(index);
    sectionRefs[index].current.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  };

  useEffect(() => {
    const handleScroll = () => {
      const position = window.pageYOffset;
      setScrollPosition(position);

      sectionRefs.forEach((ref, index) => {
        const element = ref.current;
        if (element) {
          const rect = element.getBoundingClientRect();
          if (rect.top <= 100 && rect.bottom >= 100) {
            setSelectedNavIndex(index);
          }
        }
      });
    };

    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  const handleImageChange = async (event) => {
    const file = event.target.files[0];

    if (file) {
      setImageName(file.name);

      const reader = new FileReader();
      reader.onload = (e) => {
        setImageSrc(e.target.result);
        getPredictionByImageName(file.name);
      };
      reader.readAsDataURL(file);
    } else {
      setImageName("No image selected");
      setImageSrc(null);
    }
  };

  const getPredictionByImageName = async (imageName) => {
    try {
      console.log(imageName);
      const response = await fetch(
        `${process.env.PUBLIC_URL}/Json/updated_evaluation_results.json`
      );

      const data = await response.json();
      console.log("after getting data", data);

      const prediction = data.find(
        (item) => item.image_name.toLowerCase() === imageName.toLowerCase()
      );

      console.log(prediction);
      return prediction;
    } catch (error) {
      console.error("Error fetching prediction data:", error);
      return null;
    }
  };

  useEffect(() => {
    if (inView) {
      controls.start("visible");
    }
  }, [controls, inView]);

  return (
    <div className="App relative">
      <motion.div
        className="flex align-items-center justify-center z-2 loader full-width full-heigth  absolute"
        animate={animations.preloader}
      >
        <img src={loader} alt="" className="logo-img" />
      </motion.div>
      <div className="header-container ">
        <Header
          logoImg={logoImg}
          logoText="Ai FaultDetector."
          navLinks={navLinks}
          actionText="Predict"
          textColor={textColor}
          actionBtnBgColor={actionBtnBgColor}
          btnAction={handleNavLinkClick}
          selectedNavIndex={selectedNavIndex}
          setSelectedNavIndex={setSelectedNavIndex}
          handleNavLinkClick={handleNavLinkClick}
          darkMode={darkMode.value}
          setDarkMode={darkMode}
        />
      </div>

      <motion.div
        ref={sectionRefs[0]}
        className="home-body bold z-1 full-width full-height top50 flex-column align-items-center justify-center"
        initial="hidden"
        animate="visible"
        variants={animations.homeBody}
      >
        <div className="my-20  flex  justify-center align-items-center border-text">
          <p className="">AI-Powered Default Detection</p>
        </div>
        <p className={darkMode.value ? "main-text white" : "black main-text"}>
          AI Powered Fault Detection
        </p>
        <p className="grey small-text my-10">
          Our AI-driven fault detection system uses advanced machine learning to
          identify and predict faults in real-time, reducing downtime and
          enhancing performance. Ideal for managing complex systems, it ensures
          reliability and seamless operations, keeping your systems running
          smoothly.
        </p>

        <div className="actions-container flex align-items-center justify-space-evenly bold my-20">
          <div className="action-button bg-green">
            <p
              className="btn white"
              onClick={() => {
                handleNavLinkClick(2);
              }}
            >
              Get Started
            </p>
          </div>

          <div className="action-button bg-grey">
            <p
              className="btn black"
              onClick={() => {
                handleNavLinkClick(1);
              }}
            >
              View Details
            </p>
          </div>
        </div>
      </motion.div>

      <motion.div
        ref={sectionRefs[1]}
        className="section section2 flex-column align-items-center justify-end bold"
      >
        <div className="model-card flex align-items-center justify-space-between bold bg-green relative">
          <div className="white card-text relative bold flex-column px-20 py-20 box-sizing">
            <div className="my-20  flex  justify-center align-items-center border-text bg-white w-50 shadow">
              <p className="">AI-Powered Default Detection</p>
            </div>
            <p className="sec-text">Build With Combined ML Models.</p>
            <p className="small-text">
              We preprocess and clean data, select key features, and train
              machine learning models to detect faults accurately. This
              streamlined process ensures reliable and tailored results.
            </p>

            <div className="action-button bg-white black shadow">
              <p
                className="btn "
                onClick={() => {
                  handleNavLinkClick(2);
                }}
              >
                Go To The Execution Page
              </p>
            </div>
          </div>

          <div className="card-imgs relative flex align-items-center justify-center">
            <img src={pc} alt="" className="z-2" />
            <div className="bubble bubble1 border-radius-10 absolute  bg-white shadow flex align-items-center justify-center">
              <p>AI</p>
            </div>
            <div className="bubble bubble2 border-radius-10 absolute  bg-white shadow flex align-items-center">
              <img src={sklearn} alt="" className=" mx-20" />
              <div className="bubble-text flex-column">
                <p>Decision Tree Model</p>
                <p className="small-text grey">Sklearn Library</p>
              </div>
            </div>

            <div className="bubble bubble3 border-radius-10 absolute  bg-white shadow flex-column align-items-center justify-space-evenly">
              <img src={python} alt="" className=" mx-20" />
              <div className="bubble-text flex-column my-10">
                <p>Decision Tree Model</p>
              </div>
            </div>

            <div className="back-circle shadow flex-column align-items-center">
              <p className="white sec-text relative">0.96</p>
              <p className="relative white">Accuracy</p>
            </div>
          </div>
        </div>
        <div className="tools-container flex align-items-center justify-space-evenly">
          <img src={python} alt="" />
          <img src={pycharm} alt="" />
          <img src={sklearn} alt="" />
          <img src={pyplot} alt="" />
        </div>
      </motion.div>

      <div
        ref={sectionRefs[2]}
        className="section section3 flex-column align-items-center justify-center bold"
      >
        <p className="">Let Predict And Get Your Results</p>

        <div className="form-section flex">
          <Form
            inputs={inputs}
            actionBtnText="Get Predictions Now"
            selectedNavIndex={selectedNavIndex}
            submitHandler={toggleOverlay}
            setResults={setResults}
            darkMode={darkMode.value}
          />
        </div>
      </div>

      <motion.div
        ref={sectionRefs[3]}
        className="section section5 relative flex bold align-items-center justify-center"
        initial="hidden"
        animate={controls}
      >
        <div className="flex w-full h-full">
          {/* Left Div: Description and Image Loader */}
          <div className="half-page flex flex-col align-items-center w-1/2 h-full p-5  predcition-left">
            <div className="mb-4">
              <p className="small-text my-20">
                In developing the fault detection model, we employed various
                techniques to improve performance.
              </p>

              <h2 className="text-lg font-bold">Fault Detection Model</h2>
            </div>
            <div className="image-displayer flex justify-center align-items-center border-2 border-dashed border-gray-300 h-60 bg-gray-100">
              {/* Placeholder for displaying the loaded image */}

              {imageSrc ? (
                <img src={imageSrc} alt="" className="loaded-image" />
              ) : (
                <p className="text-gray-500 ">Image will be displayed here</p>
              )}
            </div>

            <div className="action-button bg-green white m-20 relative">
              {imageSrc ? (
                <p class="btn pointer">{imageName}</p>
              ) : (
                <p class="btn pointer">Load Image</p>
              )}
              <input
                type="file"
                accept="image/*"
                className=" custom-file-input absolute"
                onChange={handleImageChange}
              />
            </div>

            <p
              className="small-text grey pointer"
              onClick={() => {
                setImageName(null);
                setImageSrc(null);
                setPredcitedClass(null);
                setRealClass(null);
              }}
            >
              Reset
            </p>
          </div>

          {/* Right Div: Descriptive Texts and Result Display */}
          <div className="flex flex-column  predcition-right ">
            <p className="small-text">
              In developing the fault detection model, we employed various
              techniques to improve performance.
            </p>
            <h2 className="text-2xl font-bold mt-8 mb-4  ">
              The model reached an accuracy of approximately{" "}
              <span className="green">88%</span> after fine-tuning.
            </h2>
            <p className="">
              Initially, we used a Transformer-based model with data
              augmentation and class imbalance handling via SMOTE. Despite
              achieving moderate accuracy, we further enhanced the model using
              K-fold cross-validation to combine results and stabilize
              performance. We also implemented early stopping, learning rate
              scheduling, and data augmentation strategies to prevent
              overfitting and boost generalization. The combination of these
              techniques led to improved model robustness and higher validation
              accuracy, demonstrating the efficacy of the folds combination and
              augmentation process.
            </p>

            <div className="relative m-20 text-align-canter">
              <motion.p
                initial={{ opacity: 0 }}
                animate={currentStep === 1 ? { opacity: 1 } : { opacity: 0 }}
                transition={{ duration: 0.5 }}
                className="small-text grey m-20 absolute loading-text"
              >
                Sending Request...
              </motion.p>

              <motion.p
                initial={{ opacity: 0 }}
                animate={currentStep === 2 ? { opacity: 1 } : { opacity: 0 }}
                transition={{ duration: 0.5 }}
                className="small-text grey m-20 absolute loading-text"
              >
                Loading Your Prediction Results...
              </motion.p>

              <motion.p
                initial={{ opacity: 0 }}
                animate={currentStep === 3 ? { opacity: 1 } : { opacity: 0 }}
                transition={{ duration: 0.5 }}
                className="small-text grey m-20 absolute loading-text"
              >
                Displaying Your Prediction Results...
              </motion.p>
            </div>

            <div className=" mt-8 flex items-center justify-center center-text">
              {/* Placeholder for displaying the predicted class and correctness */}
              <div className="relative img-res">
                <h3 className="text-gray-500 m-20">Real Class</h3>
                <motion.h3
                  className="text-gray-500 m-20 relative"
                  initial={{ opacity: 0 }}
                  animate={
                    !showLoader
                      ? { opacity: 1 }
                      : showRealClass
                      ? { opacity: 1 }
                      : { opacity: 0 }
                  }
                >
                  {realClass !== null ? realClass : "-"}
                </motion.h3>
                <motion.img
                  src={loader}
                  className="logo-img absolute"
                  initial={{ opacity: 0 }}
                  animate={showLoader ? { opacity: 1 } : { opacity: 0 }}
                  transition={{ duration: 0.5 }}
                />
              </div>
              <div className="relative img-res">
                <h3 className="text-gray-500 m-20">Predicted Class</h3>

                <motion.h3
                  className="text-gray-500 m-20 relative"
                  initial={{ opacity: 0 }}
                  animate={
                    !showLoader
                      ? { opacity: 1, transition: { delay: 2 } }
                      : showRealClass
                      ? { opacity: 1 }
                      : { opacity: 0 }
                  }
                >
                  {predcitedClass !== null ? predcitedClass : "-"}
                </motion.h3>

                <motion.img
                  src={loader}
                  className="logo-img absolute"
                  initial={{ opacity: 0 }}
                  animate={
                    showLoader
                      ? { opacity: 1 }
                      : { opacity: 0, transition: { delay: 2 } }
                  }
                  transition={{ duration: 0.5 }}
                />
              </div>
            </div>
          </div>
        </div>
      </motion.div>

      <motion.div
        ref={sectionRefs[4]}
        className="section section4 relative flex bold align-items-center justify-center"
        initial="hidden"
        animate={controls}
      >
        {/* Content for section 4 */}

        <img src={phone} alt="" className=" contact-img" />
        <img
          src={logoImg}
          alt=""
          className={
            darkMode.value
              ? "absolute logo-contact inverted-image"
              : "logo-contact absolute"
          }
        />
        <p className="absolute ">Contact Our Team</p>
        <p className="back-text absolute main-text">Contact Us</p>

        <p className=" small-text absolute grey bottom0 center-text">
          copyright are protected <br /> 2024
        </p>

        <div className="absolute left-text">
          <p className="my-10">Researcher</p>
          <p>Amina naamoun</p>
          <p>Amina.naamoun@univ-constantine.dz</p>
          <p>+213 552990625</p>
          <p>Phd Student</p>
        </div>

        <div className="absolute right-text">
          <p className="my-10">Developper</p>
          <p>Tazir Mohamed Hatem</p>
          <p>hatem.tazir@univ-constantine.dz</p>
          <p>+213 552990625</p>
          <p>Phd Student</p>
        </div>
      </motion.div>

      <PageIndicator sectionsCount={4} selectedIndex={selectedNavIndex} />

      <OverlayContainer
        stat={isOverlayOpen}
        toggleOverlay={toggleOverlay}
        contentWidth="500px"
        contentHeight="30%"
      >
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1, transition: { delay: 3 } }}
        >
          <div className="results-overlay  flex align-items-center justify-center">
            <p className="bold">Predictions Result</p>
            <div className="full-width flex-column align-items-center justify-center bold">
              <div className="full-width flex align-items-center justify-space-between">
                <div className="res">
                  <p className="label">Default</p>
                  <div className=" input border flex align-items-center">
                    <p>{results[0]}</p>
                  </div>
                </div>

                <div className="res">
                  <p className="label">Default Class</p>
                  <div className="  input border flex align-items-center">
                    <p>{results[1]}</p>
                  </div>
                </div>
              </div>

              <div className="flex">
                <div className="action-button my-20 ">
                  <button type="submit" onClick={() => toggleOverlay()}>
                    Close
                  </button>
                </div>
              </div>
            </div>
          </div>
        </motion.div>

        <motion.div
          className="absolute flex justify-center align-items-center full-width border left0 top0 overlay-loader"
          initial={{ opacity: 1 }}
          animate={{ opacity: 0, transition: { delay: 3 } }}
        >
          <img src={loader} alt="" className="logo-img" />
        </motion.div>
      </OverlayContainer>
    </div>
  );
}

export default App;
