import React from "react";
import { motion } from "framer-motion";
import { animations } from "../animations/animations";
import sun from "../assets/sun.png"
import moon from "../assets/moon.png"

const Header = ({
  logoImg,
  logoText,
  navLinks,
  actionText,
  textColor,
  actionBtnBgColor,
  btnAction,
  selectedNavIndex,
  setSelectedNavIndex,
  handleNavLinkClick,
  darkMode,
  setDarkMode
}) => {
  return (
    <header className={`header-comp ${darkMode ? 'dark-mode' : 'light-mode'}`}>
      {logoImg ? (
        <motion.div
          initial={{ top: "-100px", opacity: 0 }}
          animate={animations.logo}
          style={{ color: darkMode ? "white" : "black" }}
        >
          <img src={logoImg} alt={"logo"} className={darkMode ? " inverted-image logo-img" : "logo-img" }/>
        </motion.div>
      ) : (
        <motion.div
          className="logo"
          initial={{ top: "-100px" }}
          animate={animations.logo}
          style={{ color: darkMode ? "white" : "black" }}
        >
          <p>{logoText}</p>
        </motion.div>
      )}

      <motion.nav
        initial={{ left: "-100px", opacity: 0 }}
        animate={animations.nav}
      >
        <ul>
          {navLinks.map((link, index) => (
            <motion.li
              key={index}
              className={index === selectedNavIndex ? "selected" : ""}
              onClick={() => handleNavLinkClick(index)}
              transition={{ duration: 0.3, ease: "easeInOut" }}
              animate={selectedNavIndex == index 
                ? {color:"#A1DD70"}
                : {color: darkMode ? "white" : "black"}}
            >
              <p>{link}</p>
            </motion.li>
          ))}
        </ul>
      </motion.nav>

      <motion.div
        className="flex align-items-center justify-canter bold"
        initial={{ top: "-100px", opacity: 0 }}
        animate={animations.langueAndSignUp}
        style={{ color: darkMode ? "white" : "black" }}
      >
        <motion.div className="langue" initial={{ borderColor: darkMode ? "red" : "black" }} 
        animate={{ borderColor: darkMode ? "white" : "#222831" }}
        onClick={darkMode ? setDarkMode.disable : setDarkMode.enable}
        >
          {
            !darkMode ? <img src={moon} alt={"moon"} className="langue-img"/>:
            <img src={sun} alt={"sun"} className="langue-img"/>
          }
        </motion.div>

        
        <motion.div
          className="action-button"
          initial={{ actionBtnBgColor: "#A1DD70", color: darkMode ? "black" : "white" }}
          animate={{ backgroundColor: "#A1DD70", color: darkMode ? "black" : "white" }}
          transition={{ duration: 0.3, ease: "easeInOut" }}
          onClick={()=>btnAction(2)}
        >
          <p className="btn">{actionText}</p>
        </motion.div>
      </motion.div>
    </header>
  );
};

export default Header;
